








































	import BASEURL from "../../common/baseInfo.js";
	import uniPopup from '../../components/uni-popup.vue';
	export default {
		components:{
			uniPopup
		},
		onShow() {
			let that = this;
			if (!this.isfirst) {
				that.getUserAppInfo();
			}
		},
		data() {
			return {
				avatarUrl: "",
				uerInfo: {},
				openId: '',
				showPopupBottom: false,
				checkedInfo: false,
				isfirst: true
			}
		},
		beforeCreate() {
		},
		onLoad() {
			let that = this;
			uni.getStorage({
				key: 'userInfo',
				success: function (res) {
					// console.log(JSON.parse(res));
					console.log(res.data);
					that.uerInfo = res.data;
				}
			})
			uni.getStorage({
				key: 'openIdInfo',
				success: function(res) {
					// console.log(JSON.parse(res));
					console.log(res);
					that.openId = res.data;
					that.getUserAppInfo();
				}
			})
			// this.getUserInfoFn();
  
		},
		methods: {
			getUserAppInfo() {
				let that = this;
				console.log(`${BASEURL}/khxcx-0.0.1-SNAPSHOT/findUserByOpenId.action`);
				uni.request({
					url: `${BASEURL}/khxcx-0.0.1-SNAPSHOT/findUserByOpenId.action`,
					method: 'GET',
					data: {
						openId: that.openId
					},
					success: res => {
						console.log(res);
						that.isfirst = false;
						if (res.data.data.type === '上市者') {
							that.checkedInfo = true;
						} else {
							that.checkedInfo = false;
						}
					},
					fail: (err) => {
						console.log(err);
					},
					complete: () => {}
				});
			},
			goUserInfo() {
				uni.navigateTo({
					url: '../userinfo/userinfo'
				});
			},
			bindGetUserInfo: function(e) {
			  console.log(e.detail.userInfo)
			  if (e.detail.userInfo){
			    //用户按了允许授权按钮
			  } else {
			    //用户按了拒绝按钮
			  }
			},
			checkShangshi() {
				uni.navigateTo({
					url: '../authPhone/authPhone'
				});
			},
			listInfo() {
				uni.navigateTo({
					url: '../goList/goList'
				});
			},
			goWallet() {
				uni.navigateTo({
					url: '../wallet/wallet'
				});
			},
			goMyInvest() {
				uni.navigateTo({
					url: '../myInvest/myInvest'
				});
			}
// 			getUserInfoFn () {
